import { instagramChannel, tiktokChannel, twitchChannel, youtubeChannel } from './mock';

const find = (params) => {
    console.log('API params', {params})
  const { type, id } = params;
  if (type === 'youtube') return youtubeChannel;
  if (type === 'twitch') return twitchChannel;
  if (type === 'instagram') return instagramChannel;
  if (type === 'tiktok') return tiktokChannel;
  return youtubeChannel;
};

export default find;
