import Avatar from '@buzzguru/ui/Avatar';
import Link from '@lskjs/link';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import getRequestParams from '../../../utils/getRequestParams';
import { Wrapper } from './Mention.styles';

const Mention = ({ entity }) => {
  const { item } = entity;
  const [type, setType] = useState('youtube');
  const appMentions = item?.productMentions?.app || {};
  const gameMentions = item?.productMentions?.game || {};
  const siteMentions = item?.productMentions?.site || {};
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, []);
  return (
    <Wrapper>
      <Link
        style={{ display: 'flex', margin: '0 24px', textDecoration: 'none' }}
        target="_blank"
        href={`https://buzzguru.com/${type}/channels/${item?._id}/products`}
      >
        {(appMentions?.items || [])
          .filter(({ product }) => (product.info?.image || '').length > 0)
          .splice(0, 3)
          .map(({ product }) => (
            <Avatar
              id={product?.id}
              key={product?.id}
              size={36}
              src={product?.info?.image}
              defaultAvatar="https://buzzguru.com/assets/images/product.png"
              shape="square"
              innerStyle={{
                borderRadius: 8,
                border: '2px solid #313137',
              }}
              style={{
                marginRight: -12,
                background: 'none',
              }}
            />
          ))}
        <div
          style={{
            width: 36,
            height: 36,
            borderRadius: 8,
            backgroundColor: '#4C4C56',
            fontSize: 13,
            zIndex: 1,
            color: '#ffffff',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          {`+${appMentions?.totals?.totalUniqProductsCount || 0}`}
        </div>
      </Link>
      <Link
        style={{ display: 'flex', margin: '0 24px', textDecoration: 'none' }}
        target="_blank"
        href={`https://buzzguru.com/${type}/channels/${item?._id}/sites`}
      >
        {(siteMentions?.items || []).splice(0, 3).map(({ product }) => (
          <Avatar
            id={product?.id}
            key={product?.id}
            size={36}
            src={product?.info?.image}
            defaultAvatar="https://buzzguru.com/assets/images/product.png"
            shape="square"
            innerStyle={{
              borderRadius: 8,
              border: '2px solid #313137',
            }}
            style={{
              marginRight: -12,
              background: 'none',
            }}
          />
        ))}
        <div
          style={{
            width: 36,
            height: 36,
            borderRadius: 8,
            backgroundColor: '#4C4C56',
            fontSize: 13,
            zIndex: 1,
            color: '#ffffff',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          {`+${siteMentions?.totals?.totalUniqProductsCount || 0}`}
        </div>
      </Link>
      <Link
        style={{ display: 'flex', margin: '0 24px', textDecoration: 'none' }}
        target="_blank"
        href={`https://buzzguru.com/${type}/channels/${item?._id}/games`}
      >
        {(gameMentions?.items || []).splice(0, 3).map(({ product }) => (
          <Avatar
            id={product?.id}
            key={product?.id}
            size={36}
            src={product?.info?.image}
            defaultAvatar="https://buzzguru.com/assets/images/product.png"
            shape="square"
            innerStyle={{
              borderRadius: 8,
              border: '2px solid #313137',
            }}
            style={{
              marginRight: -12,
              background: 'none',
            }}
          />
        ))}
        <div
          style={{
            width: 36,
            height: 36,
            borderRadius: 8,
            backgroundColor: '#4C4C56',
            fontSize: 13,
            zIndex: 1,
            color: '#ffffff',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          {`+${gameMentions?.totals?.totalUniqProductsCount || 0}`}
        </div>
      </Link>
    </Wrapper>
  );
};

Mention.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default Mention;
