import AccountCircleIcon from '@buzzguru/ui/assets/icons/account-circle';
import EyeIcon from '@buzzguru/ui/assets/icons/eye';
import PriceIcon from '@buzzguru/ui/assets/icons/ic-price';
import ThumbsUpIcon from '@buzzguru/ui/assets/icons/ic-thumbs-up';
import Tooltip from '@buzzguru/ui/Tooltip';
import Typography from '@buzzguru/ui/Typography2';
import T from '@lskjs/t';
import { toShort } from '@lskjs/utils/formatter';
import get from 'lodash/get';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';

import beauty from '../../../utils/beauty';
import { Wrapper } from './Stats.styles';

const Stats = ({ entity, i18 }) => {
  const { item } = entity;
  const stats = [
    {
      id: 'subscribers',
      value: toShort(Math.ceil(get(item, 'channelInfo.subscribers', 0))),
      title: i18.t('channelHeaderInfo.subscribersCount'),
      icon: <AccountCircleIcon color="#7070ff" size={20} />,
    },
    {
      id: 'avgViews',
      value: toShort(Math.ceil(get(item, 'average.analytics.views', 0))),
      title: i18.t('channelHeaderInfo.averageViewsCount'),
      icon: <EyeIcon color="#7070ff" size={20} />,
    },
    {
      id: 'avgLikes',
      value: toShort(Math.ceil(get(item, 'average.analytics.likes', 0))),
      title: i18.t('channelHeaderInfo.averageLikesCount'),
      icon: <ThumbsUpIcon color="#7070ff" size={20} />,
    },
    {
      id: 'price',
      value: `$${beauty(get(item, 'average.avgPriceMin', 0) / 100)} - $${beauty(
        get(item, 'average.avgPriceMax', 0) / 100,
      )}`,
      title: i18.t('channelHeaderInfo.intergrationCosts'),
      icon: <PriceIcon color="#7070ff" size={20} />,
    },
  ];
  return (
    <Wrapper id="stats">
      {stats.map((stat) => (
        <Tooltip key={stat.id} id={stat.id} overlay={stat.title}>
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', paddingRight: 24 }}>
            {stat.icon}
            <Typography view="extra-1" style={{ paddingLeft: 8 }} color="#ffffff">
              {stat.value}
            </Typography>
          </div>
        </Tooltip>
      ))}
    </Wrapper>
  );
};

Stats.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Stats);
