/* global window */
import PropTypes from 'prop-types';
import React, { useContext } from 'react';

import BottomContent from '../../components/BottomContent';
import TopContent from '../../components/TopContent';
import { ToolbarContext } from '../../Toolbar';

const ExtentionPage = ({ entity, app }) => {
  const ctx = useContext(ToolbarContext);
  if (!window?.__lskjs?.GM_getValue('auth')) app.redirect('/login');
  const { isOpen } = ctx;

  if (!isOpen) return null;
  return (
    <>
      <TopContent entity={entity} app={app} />
      <BottomContent entity={entity} />
    </>
  );
};

ExtentionPage.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  app: PropTypes.objectOf(Object).isRequired,
};

export default ExtentionPage;
