import DotsIcon from '@buzzguru/ui/assets/icons/dots-icon-horizontal';
import Typography from '@buzzguru/ui/Typography2';
import Button from '@lskjs/button';
import Dropdown from '@lskjs/ui/Dropdown';
import isAbsoluteExternalUrl from '@lskjs/utils/isAbsoluteExternalUrl';
import sortBy from 'lodash/sortBy';
import PropTypes from 'prop-types';
import React from 'react';

import { DropdownItem } from './Contacts.styles';
import iconMapping from './iconMapping';

const colors = {
  vk: '#4266b2',
  instagram: '#F00075',
  telegram: '#0088cb',
  fb: '#4266b2',
  twitter: '#1996ef',
  twitch: '#863fff',
  pinterest: '#e20020',
  mail: 'primary',
};
function convertTypeToIcon(array) {
  const prepared = array
    .map((baseItem, index) => {
      const item = baseItem;
      if ((item?.url || '').includes('tiktok.com')) {
        item.type = 'tiktok';
      }
      const icon = iconMapping[item.type];
      const color = colors[item.type] || null;
      if (!icon && !item.title) return null;
      let href = item.url || '';
      if (href === 'https' || href === 'http') href = '';
      if (!href) return null;
      if (!isAbsoluteExternalUrl(href)) {
        if (item.type === 'telegram') {
          href = `https://t.me/${href}`;
        } else {
          href = `https://${href}`;
        }
      }

      if (href.includes('@') && item.type !== 'tiktok') href = `mailto:${href}`;
      if (!(href.startsWith('mailto:') || isAbsoluteExternalUrl(href))) return null;

      return {
        id: `${index}-${item.type}`,
        overlay: item.title,
        href,
        icon: icon || iconMapping.web,
        color,
        componentClass: 'a',
        target: '_blank',
        rel: 'noopener noreferrer nofollow',
      };
    })
    .filter((e) => !!e);
  return sortBy(prepared, 'icon').reverse();
}

const Contacts = ({ entity }) => {
  const { item } = entity;
  const links = convertTypeToIcon(item?.channelInfo?.links || []);
  return (
    <Dropdown
      id="extention_contacts_drop"
      pull="end"
      placement="top"
      trigger={({ onClick }) => (
        <div aria-hidden onClick={onClick}>
          <Button
            style={{ width: 24, height: 24, borderRadius: 4, backgroundColor: '#4A4A4A80', marginRight: 12 }}
            view="empty"
            isRipple={false}
            icon={<DotsIcon size={18} color="#ffffff" />}
          />
        </div>
      )}
    >
      {links.map(({ overlay = 'Untitled', ...prps }) => (
        <DropdownItem {...prps} key={prps.id}>
          <Typography view="title-3">{overlay}</Typography>
        </DropdownItem>
      ))}
    </Dropdown>
  );
};

Contacts.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
};

export default Contacts;
