/* global window navigator */
import BookmarkEmptyIcon from '@buzzguru/ui/assets/icons/bookmark-empty';
import Close from '@buzzguru/ui/assets/icons/clear-big';
import Copy from '@buzzguru/ui/assets/icons/ic-list';
import Settings from '@buzzguru/ui/assets/icons/settings';
import Button from '@lskjs/button';
import Link from '@lskjs/link';
import Modal from '@lskjs/modal';
import T from '@lskjs/t';
import get from 'lodash/get';
import PropTypes from 'prop-types';
import React, { useContext, useEffect, useState } from 'react';

import { ToolbarContext } from '../../../Toolbar';
import getRequestParams from '../../../utils/getRequestParams';
import { Wrapper } from './RightContent.styles';

const RightContent = ({ app, entity }) => {
  const { item } = entity;
  const [type, setType] = useState('youtube');
  const ctx = useContext(ToolbarContext);
  const onClose = () => {
    ctx.triggerOpen();
  };
  const logout = () => {
    window?.__lskjs?.GM_setValue('auth', null);
    console.log('authToken', window?.__lskjs?.GM_getValue('auth'));
    app.redirect('/login');
  };
  const handleCopy = () => {
    const stats = [
      {
        subscribers: get(item, 'channelInfo.subscribers', 0),
      },
      {
        views: get(item, 'average.analytics.views', 0),
      },
      {
        likes: get(item, 'average.analytics.likes', 0),
      },
      {
        price: `$${get(item, 'average.avgPriceMin', 0) / 100} - $${get(item, 'average.avgPriceMax', 0) / 100}`,
      },
    ];
    navigator.clipboard.writeText(JSON.stringify(stats));
  };
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, []);
  const href = `https://buzzguru.com/${type}/channels/${item?._id || ''}`;
  return (
    <Wrapper>
      <Button
        style={{ width: 36, height: 36, borderRadius: 8, backgroundColor: '#4C4C56', marginRight: 12 }}
        view="empty"
        icon={<Copy size={18} style={{ color: '#ffffff' }} />}
        isRipple={false}
        onClick={handleCopy}
      />
      <Button
        onClick={() => console.log('Added to list')}
        iconLeft={<BookmarkEmptyIcon />}
        paint="primary"
        size="small"
        view="text"
        bordered
        style={{ marginRight: 12, height: '100%' }}
      >
        <T name="button.addToList" />
      </Button>
      <Button
        as={Link}
        paint="primary"
        href={href}
        target="_blank"
        style={{ marginRight: 12, height: '100%', paddingTop: 12 }}
      >
        <T name="extention.button.moreInsights" />
      </Button>
      <Modal
        trigger={
          <Button
            style={{ width: 36, height: 36, borderRadius: 8, backgroundColor: '#4C4C56' }}
            view="empty"
            icon={<Settings size={18} style={{ color: '#ffffff' }} />}
            isRipple={false}
          />
        }
      >
        <Modal.Content style={{ backgroundColor: '#313137', borderRadius: 8 }}>
          <Button paint="common" block style={{ width: '100%', marginTop: 48 }} onClick={logout}>
            <T name="buttons.logout" />
          </Button>
        </Modal.Content>
      </Modal>
      <Button view="empty" icon={<Close size={18} style={{ color: '#ffffff' }} />} onClick={onClose} isRipple={false} />
    </Wrapper>
  );
};

RightContent.propTypes = {
  app: PropTypes.objectOf(Object).isRequired,
  entity: PropTypes.objectOf(Object).isRequired,
};

export default RightContent;
